import { responseCache } from "./eventListenerModule";

function setUsCache(obj) {
    responseCache.setCacheUs(obj);
};

function setMetricCache(obj) {
    responseCache.setCacheMetric(obj)
};

function getUsCache() {
    return responseCache.getCacheUs();
};

function getMetricCache() {
    return responseCache.getCacheMetric()
};

async function getWeather(location, unitGroup, instruction) {
    const address = location;
    if (unitGroup === 'metric') {
        if (getMetricCache() && instruction === 'ok') {
            console.log('cached data found, returning cached data');
            return getMetricCache();
        } else {
            try {
                console.log('cached data NOT found, making a new API call:');
                const data = await fetch(`https://fg2fagig20.execute-api.sa-east-1.amazonaws.com/dev/reverse-geocode?location=${address}&unitGroup=${unitGroup}`);
                if (!data.ok) {
                    const error = new Error(data.status);
                    error.status = data.status;
                    throw error
                }
                const dataJson = await data.json();
                setMetricCache({ ...dataJson, ok: data.ok });
                return getMetricCache();
            } catch(error) {
                console.log('ERROR:', error.message);
                return { error: true, status: error.status, message: error.message };
            };
        }
    } else if (unitGroup === 'us') {
        if (getUsCache() && instruction === 'ok') {
            console.log('cached data found, returning cached data');
            return getUsCache();
        } else {
            try {
                console.log('cached data NOT found, making a new API call:');
                const data = await fetch(`https://fg2fagig20.execute-api.sa-east-1.amazonaws.com/dev/reverse-geocode?location=${address}&unitGroup=${unitGroup}`);
                if (!data.ok) {
                    const error = new Error(data.status);
                    error.status = data.status;
                    throw error
                }
                const dataJson = await data.json();
                setUsCache({ ...dataJson, ok: data.ok });
                return getUsCache();
            } catch(error) {
                console.log('ERROR:', error.message);
                return { error: true, status: error.status, message: error.message };
            };
        }
    };
};

async function returnObject(unit, address) {
    const weatherObjectMetric = await getWeather(address, 'metric', 'ok');
    const weatherObjectUs = await getWeather(address, 'us', 'ok');
    if (unit === 'metric') {
        return weatherObjectMetric;
    } else {
        return weatherObjectUs
    }
};

export { getWeather, returnObject }